@import url('https://fonts.googleapis.com/css2?family=Acme&family=Cinzel:wght@500&family=Dosis:wght@200&family=Josefin+Sans&family=Lato&family=Montserrat:wght@300&family=Noto+Serif+Devanagari:wght@100&family=Nunito&family=Open+Sans&family=Quicksand:wght@300&family=Raleway:wght@300&family=Roboto:wght@300&family=Rubik:wght@300;400&family=Ubuntu:ital,wght@1,300&display=swap');

* {
    margin: 0%;
    padding: 0%;
    font-family: 'Nunito', serif;
}

body {
    background: url("bg-image.jpg") no-repeat center center/cover;
    background-attachment: fixed;
}

.App {
    width: 100%;
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    color: white;
}

.heading {
    display: flex;
    background-color: rgb(90 7 7 / 50%);
    width: 100%;
    margin: auto;
    justify-content: center;
    align-items: center;  
    position: fixed;
    top: 0;
}

.heading h1 {
    padding-top: 0.3rem;
    font-size: 1rem;
}

.group {
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: row;
    /* height: 100px; */
    overflow: hidden;
    align-items: center;
    justify-content: center;
}

label {
    position: absolute;
    top: 0;
    left: 21%;
    color: rgba(255, 255, 255, 0.5);
    font-size: 1.5rem;
    cursor: text;
    transition: 0.25s ease;

}

input {
    display: block;
    width: 100%;
    /* height: 50px; */
    margin: auto;
    padding-top: 2px;
    border: none;
    border-radius: 0;
    color: white;
    background: transparent;
    font-size: 1.5rem;
    transition: 0.3s ease;
}

input:valid~label {
    /* top: 0; */
    font-size: 1.5rem;
    color: rgba(255, 255, 255, 0.5);
    padding: 2px 2px;
    width: 60%;
    margin: auto;
}

input:focus {
    outline: none;
}

input:focus~label {
    font-size: 0.1px;
    color: #2196f3;
}

input:focus~.bar:before {
    transform: translateX(0);
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 1000px #333 inset;
    -webkit-text-fill-color: white !important;
}

.bar {
    background: rgba(255, 255, 255, 0.5);
    content: "";
    width: 100%;
    margin: auto;
    height: 1px;
    transition: 0.3s ease;
    position: relative;
}


::selection {
    background: rgba(33, 150, 243, 0.3);
}

.searchBtn {
    width: 5rem;
    height: 2.5rem;
    color: white;
    font-size: 1.2rem;
    border: 2px solid rgb(255, 86, 56);
    background: transparent;
    border-radius: 6px;
}

.searchBtn:hover {
    background-color: rgb(255, 86, 56);
}

.searchBtn:disabled {
    color: rgb(209, 186, 186);
}


.cityName {
    font-size: 2.5rem;
    position: relative;
    left: 14px;

}

.tempData .temp {
    font-size: 7rem;
    color: rgb(255, 86, 56);
}

.tempUnit p {
    font-size: 30px;
}


.weatherDetailsContainer {
    background-color: rgba(32, 22, 22, 0.6);
    border-radius: 10px;
}

.containerDiv {
    margin-top: 4rem;
}


.max-min {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 8px;
}

.font-size-large {
    font-size: 1.5rem;
}

.font-size-small {
    font-size: 0.8rem;
}

.font-size-medium{
    font-size: 1.1rem;
}

.desc-item{
    display: grid;
    justify-items: center;
}

.footer {
    position: fixed;
    bottom: 0;
   width: 100%;
    color: rgb(233, 202, 202);
    background-color: rgb(81, 8, 8);
    font-size: 10px;
    padding: 3px;
    text-align: center;
}